import { createRouter, createWebHistory } from 'vue-router'

import Home from '../views/Home.vue';
import Bandersnatch from '../views/Bandersnatch.vue'

const routes = [
    { 
        path: '/', 
        name: 'Home',
        component: Home 
    },
    { 
        path: '/bandersnacth/:id',
        props : true, 
        component: Bandersnatch
    },
    {
        path : '/:catchAll(.*)',
        name : 'NotFound',
        component : Home
    },
]
  
const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router;