<template>
    <div>
      <h1 v-if="!isFinal" class="title text-center my-5">             
        Capitulo <h1 class="text-danger title d-inline"> {{ capitulo }} </h1>        
      </h1>
      <h1 v-else class="title text-center my-5">
        Final <h1 class="text-danger title d-inline"> {{ tituloFinal }} </h1> 
      </h1>
      <div class="escenario-container">
        <div class="row p-2 mb-3">
          <div class="col-md-7 p-0">
            <img class="escenario" :src="escenario">     
          </div>
          <div class="col-md-5 p-0">
            <div class="escenario-quest darken p-3">
              <h5 class="text-center">{{ fecha }}</h5>
              <hr class="my-4">     
              <perfect-scrollbar>
                <p class="m-0 text-center">{{descripcion}}</p>
                <hr class="mt-4">                
                <div class="w-100">
                  <div class="d-flex list-border mt-3" v-for="(mensaje,index) in conversacion" v-bind:key="`msg_`+index">
                    <div class="mx-3">
                      <img class="character rounded-circle" :src="mensaje.personaje.imagen">
                    </div>
                    <div>
                      <h4 class="mb-1"> 
                        {{mensaje.personaje.nombre}}
                      </h4>
                      <p>
                        {{mensaje.dialogo}}
                      </p>
                    </div>
                  </div>
                </div>
              </perfect-scrollbar>   
            </div>
          </div>          
        </div>

        <h6 class="my-4 text-center">opciones: </h6>

        <div class="d-flex justify-content-center flex-wrap" v-if="!isFinal">
          <router-link v-for="(opcion, index) in opciones" :to="`/bandersnacth/${opcion.id}`" class="btn btn-light text-danger btn-lg px-3 mx-3 mb-3" v-bind:key="`opcion_`+index">
            {{opcion.texto}}
          </router-link>
        </div>
        <div class="d-flex justify-content-center flex-wrap" v-else>          
          <router-link to="/" class="btn btn-light text-danger btn-lg px-3 mx-3 mb-3">
            Finalizar Historia
          </router-link>        
        </div>
      </div>      
    </div>
</template>

<script>

import Engine from '@/assets/controller/Engine';

export default {
  name: 'Bandersnatch',
  props: {
    id: String
  },
  data(){
      return {
          capitulo : 0,
          isFinal : false,    
          tituloFinal : "",      
          escenario : "",
          fecha : "",
          descripcion : "",
          conversacion : [            
          ],
          opciones : [            
          ]          
      }
  },
  mounted(){
    this.setEvento(this.id)
  },
  methods: {
    setEvento(id){
      let evento = Engine.getEvento(id);
      
      if(!evento){
        let r = this.$router.resolve({
            name: 'Home'
        });
        window.history.replaceState({}, 'bandersnatch', '/');
        window.location.assign(r.href)
      }

      this.capitulo = evento.capitulo;
      this.isFinal = evento.isFinal;
      this.tituloFinal = evento.tituloFinal;
      this.escenario = evento.escenario;
      this.fecha = evento.fecha;
      this.descripcion = evento.descripcion;
      this.conversacion = evento.conversacion;
      this.opciones = evento.opciones;

      console.log(evento);
    }
  },
  beforeRouteUpdate(to, from, next) {
    this.setEvento(to.params.id);
    this.$forceUpdate();
    next()
  },
}

</script>

<style>
  .ps {
    height: 300px;
  }

  .character{
    height: 35px;
    width: 35px;
  }

  .escenario{
    border-radius: 1em 0 0 1em;
    width: 100%;
    aspect-ratio: 16/9;
    object-fit: cover;
  }

  .escenario-container{
    padding: 0 140px;
  }

  .list-border{
    border-bottom: 1px solid rgb(91, 91, 91);
  }

  .escenario-quest{
    border-radius: 0 1em 1em 0;
    width: 100%;
    height: 100%;
  }

  @media (max-width: 768px) {
    .escenario{
      border-radius: 1em 1em 0 0;
      width: 100%;
      aspect-ratio: 16/9;
    }
    
    .escenario-quest{
      border-radius: 0 0 1em 1em;
      width: 100%;
      height: 100%;
    }

    .escenario-container{
      padding: 0 20px;
    }

    .ps {
      height: 200px;
    }
  }
</style>